/* 容器 */
.cart-button-container {
  position: fixed;
  right: 24px;
  bottom: 32px;
  z-index: 9999;
  display: flex;
  flex-direction: column;
  align-items: center;
}

/* 提示牌 */
.cart-tooltip {
  position: absolute;
  bottom: 70px;
  right: -10px;
  animation: float 2s ease-in-out infinite, fadeIn 0.5s ease-out;
}

.tooltip-content {
  background: linear-gradient(135deg, #fbbf24 0%, #f59e0b 100%);
  color: white;
  padding: 8px 16px;
  border-radius: 20px;
  font-size: 14px;
  font-weight: 600;
  white-space: nowrap;
  box-shadow: 0 4px 12px rgba(245, 158, 11, 0.3);
  display: flex;
  align-items: center;
  gap: 6px;
  position: relative;
}

.tooltip-text {
  display: inline-block;
}

.tooltip-emoji {
  font-size: 18px;
  animation: rotate 2s ease-in-out infinite;
}

/* 箭头 */
.tooltip-arrow {
  color: #f59e0b;
  margin-top: -4px;
  animation: bounce 1.5s ease-in-out infinite;
  display: flex;
  justify-content: center;
}

/* 购物车按钮 */
.cart-fab {
  width: 56px;
  height: 56px;
  right: 28px;
  bottom: 15px;
  border-radius: 50%;
  background: linear-gradient(135deg, #84cc16 0%, #22c55e 100%);
  color: #fff;
  border: none;
  box-shadow: 0 4px 16px rgba(34,197,94,0.18);
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.5rem;
  cursor: pointer;
  transition: box-shadow 0.2s, transform 0.2s, background 0.2s;
  position: relative;
}

.cart-fab:hover {
  box-shadow: 0 8px 24px rgba(34,197,94,0.28);
  transform: scale(1.08);
  background: linear-gradient(135deg, #65a30d 0%, #15803d 100%);
}

/* 动画效果 */
@keyframes float {
  0%, 100% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-5px);
  }
}

@keyframes bounce {
  0%, 100% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(5px);
  }
}

@keyframes rotate {
  0%, 100% {
    transform: rotate(0deg);
  }
  25% {
    transform: rotate(-10deg);
  }
  75% {
    transform: rotate(10deg);
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* 响应式调整 */
@media (max-width: 480px) {
  .cart-button-container {
    right: 16px;
    bottom: 24px;
  }
  
  .tooltip-content {
    font-size: 12px;
    padding: 6px 12px;
  }
  
  .cart-fab {
    width: 48px;
    height: 48px;
  }
} 